:root {
  --back_1: #F6F5F5;
  --back_2: #D3E0E3;
  --back_top: white;
  --back_bottom: black;
  --font_1: #1687A7;
  --font_2: #276678;
}

.App_wrap {
  width: 100%;
}

.App_main {
  /* border: 2px solid black; */
  max-width: 1000px;
  width: 95%;
  margin: auto;
}

.App_side>h2 {
  text-align: center;
  padding: 0;
  margin: 20px 0;
}

.App_mail {
  width: 200px;
  height: 50px;
  margin: 0px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.App_mail_h2 {
  width: 200px;
  /* height: 50px; */
  margin: 0px 0 0 0;
  padding: 0;
  text-align: center;
}

.App_top {
  background-color: var(--back_top);
  width: calc(100% - 20px);
  padding: 10px;
  margin: auto;
}

.App_top>h1 {
  /* background-color: red; */
  text-align: center;
  width: calc(80% - 20px);
  padding: 10px;
  margin: 10px auto;
}

.App_bottom {
  background-color: var(--back_bottom);
  color: white;
  width: calc(100% - 20px);
  padding: 10px;
  text-align: center;
}

.App_part1 {
  background-color: var(--back_1);
  width: calc(100% - 20px);
  padding: 10px;
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
}

.App_part1_txt {
  width: calc(75% - 60px);
  padding: 20px;
  padding-left: 40px;
  /* background-color: orange; */
}

.App_part1_img {
  width: calc(25% - 20px);
  padding: 10px;
  /* background-color: #1687A7; */
}

.App_part1_special {
  /* background-color:  var(--back_1); */
  /* background-image: linear-gradient(to right, var(--back_1), var(--back_1) 75%, white 75%, white); */
  width: calc(100% - 0px);
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 30px;
}

.App_part1_txt_special {
  width: calc(75% - 70px);
  padding: 30px 20px;
  padding-left: 50px;
  /* background-color: red; */
  background-color: var(--back_1);
}

.App_part1_img_special {
  width: calc(25% - 20px);
  padding: 10px;
  /* background-color: #1687A7; */
}



.App_part1_special_side {
  background-color: var(--back_1);
  /* border: 2px solid var(--font_2); */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 950px) {
  .App_part1_special {
    /* background-color:  var(--back_1); */
    /* background-image: linear-gradient(to right, var(--back_1), var(--back_1) 75%, white 75%, white); */
    width: calc(100% - 0px);
    padding: 0px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    margin-bottom: 0px;
  }

  .App_part1_txt_special {
    width: calc(100% - 70px);
    padding: 30px 20px;
    padding-left: 50px;
    /* background-color: red; */
    background-color: var(--back_1);
    margin-bottom: 30px;
  }

  .App_part1_img_special {
    width: calc(25% - 20px);
    padding: 10px;
    /* background-color: #1687A7; */
  }

  .App_part1_special_side {
    background-color: var(--back_2);
    /* border: 2px solid var(--font_2); */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    padding: 0 50px;
    /* height: 200px */
  }
}

@media (max-width: 750px) {
  .App_part1_special {
    /* background-color:  var(--back_1); */
    /* background-image: linear-gradient(to right, var(--back_1), var(--back_1) 75%, white 75%, white); */
    width: calc(100% - 0px);
    padding: 0px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    margin-bottom: 0px;
  }

  .App_part1_txt_special {
    width: calc(100% - 70px);
    padding: 30px 20px;
    padding-left: 50px;
    /* background-color: red; */
    background-color: var(--back_1);
    margin-bottom: 30px;
  }

  .App_part1_img_special {
    width: calc(25% - 20px);
    padding: 10px;
    /* background-color: #1687A7; */
  }

  .App_part1_special_side {
    background-color: var(--back_2);
    /* border: 2px solid var(--font_2); */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto 30px auto;
    padding: 0 50px 5px 50px;
    width: 110px;
    height: 170px;
  }

  .App_part1_special_side>h2 {
    margin: 0;
    padding: 10px 0;
  }
}
@media (max-width: 400px) {
  .App_part1_special_side {
    background-color: var(--back_2);
    /* border: 2px solid var(--font_2); */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto 30px auto;
    padding: 15px 0px 25px 0px;
    width: 100%;
    height: 170px;
  }
}
.App_part2 {
  background-color: var(--back_2);
  width: calc(100% - 20px);
  padding: 10px;
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 30px;
}

.App_part2_txt {
  width: 85%;
  padding: 10px 20px;
  /* background-color: red; */
}

.App_part2_img1 {
  width: calc(25% - 20px);
  padding: 10px;
  /* background-color: #1687A7; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.App_icc_car{
  width: 100%;
  height: 50px;
  /* background-color: red; */
  color: white;
  color:var(--font_2);
}

.App_part2_img2 {
  width: calc(25% - 20px);
  padding: 10px;
  /* background-color: #1687A7; */
  display: flex;
  align-items: center;
  justify-content: center;
}
.App_icc_car{
  width: 100%;
  height: 50px;
  /* background-color: red; */
  color: white;
  color:var(--font_2);
}

.App_part1_txt>h2, .App_part1_txt_special>h2 {
  color: var(--font_1);
}

.App_part2_txt>h2 {
  color: var(--font_2);
}

.App_part1_txt>h2, .App_part2_txt>h2, .App_part1_txt_special>h2 {
  padding: 0px;
  /* background-color: red; */
  margin: 10px auto;
  width: 100%;
}

.App_main_p {
  padding: 10px 0;
  /* background-color: red; */
  margin: 0px auto;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
}

.App_main_p_span1 {
  color: var(--font_1);
  font-size: 20px;
  font-weight: 600;
}

.App_main_p_span2 {
  color: var(--font_2);
  font-size: 20px;
  font-weight: 600;
}

@media (max-width: 700px) {

  .App_part1 {
    background-color: var(--back_1);
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;
  }

  .App_part1_txt {
    width: calc(100% - 40px);
    padding: 20px;
    /* background-color: orange; */
  }

  .App_part2 {
    background-color: var(--back_2);
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    flex-direction: row-reverse;
    margin-bottom: 30px;
  }

  .App_part2_txt {
    width: calc(100% - 40px);
    padding: 10px 20px;
    /* background-color: red; */
  }

  .App_part1_special {
    /* background-color:  var(--back_1); */
    /* background-image: linear-gradient(to right, var(--back_1), var(--back_1) 75%, white 75%, white); */
    width: calc(100% - 0px);
    padding: 0px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 30px;
  }

  .App_part1_special {
    /* background-color:  var(--back_1); */
    /* background-image: linear-gradient(to right, var(--back_1), var(--back_1) 75%, white 75%, white); */
    width: calc(100% - 0px);
    padding: 0px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    margin-bottom: 0px;
  }

  .App_part1_special {
    /* background-color:  var(--back_1); */
    /* background-image: linear-gradient(to right, var(--back_1), var(--back_1) 75%, white 75%, white); */
    width: calc(100% - 0px);
    padding: 0px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    margin-bottom: 0px;
  }
}

ul {
  list-style-type: none;
  /* padding: 0; */
  margin: 0;
}

.App_icc_ul {
  padding: 0 15px 0 0px;
